//
// Isotope Styles
// --------------------------------------------------

[data-isotope-layout] {
	display: block;
	transition: .4s all ease;
	min-height: 160px;
	z-index: 0;

	&:after {
		content: '';
		position: absolute;
		margin-top: $isotope-item-offset / 2;
		width: 64px;
		height: 64px;
		top: 50%;
		left: 50%;
		background-image: url('data:image/png;base64,');
		background-position: -1152px 0;
		animation: 0.7s sprite-animation steps(18) infinite;
		transition: .4s all ease;
		@include transform(translate(-50%, -50%));
	}

	.row {
		margin-bottom: -$isotope-item-offset;

		> [class*="col-"] {
			margin-top: $isotope-item-offset;
		}

		@media (min-width: $screen-md-min) {
			> [class*="col-"]:nth-child(1),
			> [class*="col-"]:nth-child(2),
			> [class*="col-"]:nth-child(3) {
				margin-top: 0;
			}
		}
	}

	.row-narrow {
		margin-bottom: -$cell-spacing-narrow;

		> [class*="col-"] {
			margin-top: $cell-spacing-narrow;
		}
	}

	.row-condensed {
		margin-bottom: 0;

		> [class*="col-"] {
			margin-top: 0;
		}
	}

	[class*="col-"] {
		display: block;
		opacity: 0;
		will-change: transform;
		backface-visibility: hidden;
		transition: .1s opacity ease-in;

		@media (max-width: $screen-xs-min - 1) {
			max-width: 100%;
		}
	}

	&.isotope--loaded {
		[class*="col-"] {
			opacity: 1;
		}

		&:after {
			opacity: 0;
			visibility: hidden;
		}
	}
}

// offset for 1800

.isotope-xl {
	@media (max-width: $screen-sm-min - 1) {
		.row > [class*="col-"] .row {
			margin-bottom: -$isotope-item-offset;

			> [class*="col-"] {
				margin-top: 35px;
			}
		}
	}
	@media (min-width: $screen-xl-min) {
		.row > [class*="col-"] .row {
			margin-bottom: -$isotope-item-offset;

			> [class*="col-"] {
				margin-top: 66px;
			}
		}
	}
}

// Isotope filter base styles

.isotope-filters {
	position: relative;
	display: inline-block;

	&-list {
		display: none;
		position: absolute;
		top: $isotope-filters-list-position-top;
		right: $isotope-filters-list-position-right;
		padding: $isotope-filters-list-padding;
		margin: $isotope-filters-list-offset;
		z-index: 10;
		float: left;
		min-width: $isotope-filters-list-min-width;
		font-size: $isotope-filters-list-size;
		text-align: left;
		background-color: $isotope-filters-list-background;
		border-radius: $isotope-filters-list-border-radius;
		box-shadow: $isotope-filters-list-box-shadow;
		> li {
			display: block;
			> a {
				display: block;
				position: relative;
				clear: both;
				color: $isotope-filters-list-link-color;
				white-space: nowrap;
				line-height: $isotope-filters-list-link-line-height;
				padding: $isotope-filters-list-link-padding;
				font-family: $font-family-accent;
				font-weight: 400;

				@media (min-width: $screen-lg-min) {
					letter-spacing: 0.06em;
				}

				&.active, &:hover {
					background-color: $isotope-filters-list-link-hover-background;
				}
			}
		}
		&.active {
			display: block;
		}
	}
	&-toggle {
		&.active:focus {
			outline: none;
		}
	}
	&-title {
		display: none;
	}
	@media (min-width: $screen-xs-min) {
		&-list {
			right: auto;
			left: 0;
		}
	}
}

// Isotope filter horizontal styles

.isotope-filters-horizontal,
.isotope-filters-vertical {
	@media (min-width: $screen-md-min) {
		padding-left: 15px;
		padding-right: 15px;
		max-width: 1170px;
		margin-left: auto;
		margin-right: auto;
		width: 100%;
		text-align: left;
		> ul > li {
			width: 100%;
			> ul {
				width: 100%;
			}
		}
	}

	&:not(.isotope-filter-collapse-lg) {
		@media (min-width: $screen-md-min) {
			.isotope-filters-list {
				position: static;
				display: inline-block;
				float: none;
				padding: 0;
				background-color: transparent;
				border-radius: 0;
				box-shadow: none;
				margin-bottom: 0;
				border-bottom: $isotope-filters-list-md-border-bottom;
				> li {
					display: inline-block;
					> a {
						display: inline-block;
						text-transform: uppercase;
						padding: $isotope-filters-list-link-md-padding;
						font-size: 16px;
						color: $gray-dark;
						@include transition(0.3s all ease-in);
						&:after {
							content: '';
							position: absolute;
							bottom: $isotope-filters-list-link-after-md-position-bottom;
							left: $isotope-filters-list-link-after-md-position-left;
							width: 0;
							height: 1px;
							background: $brand-java;
							transition: .4s;
						}
						&.active, &:hover {
							color: $isotope-filters-list-link-md-hover-color;
							background-color: $isotope-filters-list-link-md-hover-background;
						}
						&.active:after {
							left: 0;
							width: 100%;
						}
					}
					+ li {
						margin-left: 7px;
					}
				}
			}

			.isotope-filters-toggle {
				display: none;
			}
		}

		@media (min-width: $screen-lg-min) {
			.isotope-filters-list > li > a {
				font-size: 18px;
			}
		}
	}
}

// Isotope filter vertical styles

.isotope-filters.isotope-filters-vertical {
	@media (min-width: $screen-lg-min) {
		.isotope-filters-list {
			margin-bottom: 0;
			padding: 0;
			border-bottom: none;
			border-right: $isotope-filters-vertical-list-border-left;
			> li {
				display: block;
				margin-top: 0;
				text-align: right;
				> a {
					padding: $isotope-filters-vertical-list-link-padding;
					font-size: 18px;
					line-height: (24/18);

					&:after {
						bottom: 50%;
						left: auto;
						right: -1px;
						width: 1px;
						height: 0;
						background: $brand-primary;
					}

					&.active {
						color: $gray-darker;
						&:after {
							bottom: 0;
							width: 1px;
							height: 100%;
							left: auto;
							right: -1px;
						}
					}
				}
				& + li {
					margin-left: 0;
					margin-top: $isotope-filters-list-item-offset-top;
				}
			}
		}
		.isotope-filters-title {
			display: block;
		}
	}
}

// Isotope collapse lg

.isotope-filter-collapse-lg {
	@media (min-width: $screen-lg-min) {
		.isotope-filters-list {
			position: static;
			display: inline-block;
			float: none;
			padding: 0;
			background-color: transparent;
			border-radius: 0;
			box-shadow: none;
			margin-bottom: 0;
			border-bottom: $isotope-filters-list-md-border-bottom;
			> li {
				display: inline-block;
				> a {
					display: inline-block;
					text-transform: uppercase;
					padding: $isotope-filters-list-link-md-padding;
					color: $gray-dark;
					@include transition(0.3s all ease-in);
					&:after {
						content: '';
						position: absolute;
						bottom: $isotope-filters-list-link-after-md-position-bottom;
						left: $isotope-filters-list-link-after-md-position-left;
						width: 0;
						height: 1px;
						@include bg-variant-custom(linear-gradient(90deg, $brand-primary 0%, $brand-dollar-bill 100%));
						transition: .4s;
					}
					&.active, &:hover {
						color: $isotope-filters-list-link-md-hover-color;
						background-color: $isotope-filters-list-link-md-hover-background;
					}
					&.active:after {
						left: 0;
						width: 100%;
					}
				}
				+ li {
					margin-left: $isotope-filters-list-item-offset-left;
				}
			}
		}

		.isotope-filters-toggle {
			display: none;
		}
	}
}